const axios = require("axios"),
  rxjs = require("rxjs");

let subject = new rxjs.Subject(),
  commandIdx = 0;
const COMMANDS = [
  "download cv",
  "preview cv",
  "whoami",
  "skills",
  "experience",
  "github",
  "linkedin",
  "clear",
  "exit"
];

const setIndex = command => {
  let tmp = COMMANDS.findIndex(element => {
    return element === command;
  });

  commandIdx = tmp === -1 ? 0 : tmp - 1;
};

const terminal = (command, { externalLinks, commands }, callback) => {
  const {
    cvUrl,
    downloadFile,
    cvUrlOpen,
    linkedInUrl,
    githubUrl
  } = externalLinks;
  const { whoami, skills, experience, help } = commands;
  command = command.trim();

  setIndex(command);

  switch (command) {
    case "download cv":
      downloadCV(cvUrl, downloadFile);
      callback({ info: "Downloading CV ..." });
      break;

    case "preview cv":
      openUrl(cvUrlOpen);
      callback({ info: "Opened CV ..." });
      break;

    case "whoami":
      callback({ info: whoami });
      break;

    case "skills":
      callback({
        info: skills,
        component: "skills"
      });
      break;

    case "experience":
      callback({
        info: experience,
        component: "experience"
      });
      break;

    case "clear":
      callback({ info: "clear" });
      break;

    case "exit":
      callback({ info: "exit" });
      break;

    case "--help":
      callback({
        info: help,
        component: "commandPreview"
      });
      break;

    case "linkedin":
      openUrl(linkedInUrl);
      callback({ info: "Opened LinkedIn ..." });
      break;

    case "github":
    case "projects":
      openUrl(githubUrl);
      callback({ info: "Opened Github ..." });
      break;

    case "":
      callback("");
      break;

    default:
      callback({
        info: `${command}: command not found`
      });
      break;
  }
};

/**
 * https://sites.google.com/site/gdocs2direct/?key5sk1=30c1fdd27dd0c371ccfac0fd5f4c81d8b0e2e543
 * Link for creating 'downloadable' google drive link
 */
const downloadCV = (cvUrl, cvName) => {
  return axios({
    url: cvUrl,
    method: "GET",
    responseType: "blob"
  })
    .then(response => {
      const url = window.URL.createObjectURL(new Blob([response.data])),
        link = document.createElement("a");

      link.href = url;
      link.setAttribute("download", cvName); //or any other extension
      document.body.appendChild(link);

      link.click();
      subject.next("Done");
    })
    .catch(() => {
      subject.next("error");
    });
};

const openUrl = url => {
  const link = document.createElement("a");

  link.href = url;
  link.setAttribute("target", "_blank"); //or any other extension
  document.body.appendChild(link);

  link.click();
};

const navigateCommands = index => {
  commandIdx -= index;

  if (commandIdx >= COMMANDS.length) commandIdx = COMMANDS.length - 1;

  if (commandIdx === -1) commandIdx = 0;

  return COMMANDS[commandIdx];
};

module.exports = {
  terminal,
  event: subject,
  navigateCommands
};
