<template>
  <sidebar-menu
    v-click-outside="closeMenu"
    :hideToggle="true"
    :collapsed="collapsed"
    :disableHover="true"
    :menu="menu"
    @item-click="onItemClick"
  >
    <div class="header" slot="header">
      <img src="../assets/icons/tabIcon.png" />
    </div>
    <div class="footer" slot="footer" v-if="!collapsed">
      Created by
      <a :href="externalLinks.githubUrl" target="_blank">Me</a>
    </div>
  </sidebar-menu>
</template>

<script>
import ClickOutside from "vue-click-outside";

export default {
  name: "sideMenu",
  props: [
    "collapsed",
    "closeMenu",
    "leave",
    "handleCommand",
    "externalLinks",
    "disableClick"
  ],
  directives: {
    ClickOutside
  },
  methods: {
    onItemClick(event, item) {
      if (this.leave || this.disableClick) return;

      switch (item.title) {
        case "Home":
          this.$props.handleCommand("exit");
          break;

        case "Download CV":
          this.$props.handleCommand("download cv");
          break;

        case "Github":
          this.$props.handleCommand("github");
          break;

        case "LinkedIn":
          this.$props.handleCommand("linkedin");
          break;

        case "Who Am I":
          this.$props.handleCommand("whoami");
          break;

        case "Skills":
          this.$props.handleCommand("skills");
          break;

        case "Experience":
          this.$props.handleCommand("experience");
          break;

        default:
          break;
      }
    }
  },
  data() {
    return {
      menu: [
        {
          title: "Home",
          icon: "fa fa-home"
        },
        {
          title: "Who Am I",
          icon: "fa fa-user"
        },
        {
          title: "Skills",
          icon: "fa fa-cogs"
        },
        {
          title: "Experience",
          icon: "fa fa-history"
        },
        {
          title: "Github",
          icon: "fab fa-github"
        },
        {
          title: "LinkedIn",
          icon: "fab fa-linkedin"
        },
        {
          title: "Download CV",
          icon: "fa fa-file-pdf"
        }
      ]
    };
  }
};
</script>

