  text-align: center;
}

.vsm--header {
  display: none;
}

.v-sidebar-menu.vsm_collapsed .vsm--mobile-bg {
  background-color: #2a2a2e !important;
  border-radius: 0 15px 15px 0;
  width: 80%;
  display: none;
}

.vsm--list {
  padding-top: 20px;
}

.vsm--icon {
  background-color: transparent !important;
  font-size: 25px;
}

