<template>
  <span id="startingMessage"></span>
</template>

<script>
import Typed from "typed.js";

export default {
  name: "welcomeMessage",
  props: ["message", "onComplete"],
  mounted() {
    new Typed("#startingMessage", {
      strings: [this.message],
      typeSpeed: 10,
      showCursor: false,
      onComplete: () => this.onComplete()
    });
  }
};
</script>

<style scoped>
#startingMessage {
  max-width: 80%;
}
</style>
