<template>
  <v-container fluid>
    <v-row @click="collapsed = !collapsed">
      <v-col align="center" v-if="loading">
        <v-progress-circular :size="70" class="loader" indeterminate />
      </v-col>
      <fragment v-else-if="!loading && !error">
        <sideMenu
          :externalLinks="jsonData.externalLinks"
          :collapsed="collapsed"
          :leave="leave"
          :closeMenu="closeMenu"
          :handleCommand="handleCommand"
          :disableClick="!showPrompt"
        />
        <v-col class="terminal">
          <v-row>
            <!-- jsonData.terminal.startingMessage -->
            <welcomeMessage
              :message="jsonData.terminal.startingMessage"
              :onComplete="
                () => {
                  this.showPrompt = true;
                }
              "
            />
          </v-row>
          <v-row
            class="commandHistory"
            v-for="(command, index) in commands"
            :key="index"
            ref="messageDisplay"
          >
            <component :is="command.component" :command="command" />
          </v-row>

          <v-row v-if="showPrompt">
            <div class="prompt">{{ promptMessage }}</div>
            <input
              ref="terminalInput"
              v-on:blur="setAutoFocus"
              v-click-outside="setAutoFocus"
              class="terminalInput"
              type="text"
              v-on:keydown.13="handleCommand(null)"
              v-on:keydown.38="navigateCommands(-1)"
              v-on:keydown.40="navigateCommands(1)"
              v-model="newCommand"
              :disabled="disabledTerminal"
              autofocus
            />
          </v-row>
          <v-row>
            <span v-if="leave" class="prompt">{{ promptMessage }}</span>
            <span class="historyText" id="leaveMessage"></span>
          </v-row>
          <v-row class="blank_line"></v-row>
        </v-col>
      </fragment>
      <v-col align="center" v-else>
        <errorHandler />
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import ClickOutside from "vue-click-outside";
import Typed from "typed.js";
import * as handler from "../terminal";
import router from "../router";
import sideMenu from "./menu";
import welcomeMessage from "./welcomeMessage";
import { mapGetters } from "vuex";
import singlePrint from "./commands/singlePrint";
import errorHandler from "./errors/errorHandler";
import commandPreview from "./commands/commandPreview";
import experience from "./commands/experience";
import skills from "./commands/skills";
import promptMessages from "../services/promptMessages";

export default {
  name: "terminal",
  mixins: [promptMessages],
  components: {
    sideMenu,
    welcomeMessage,
    singlePrint,
    errorHandler,
    commandPreview,
    experience,
    skills
  },
  directives: {
    ClickOutside
  },
  computed: {
    ...mapGetters(["jsonData", "loading", "error"])
  },
  data() {
    return {
      commands: [],
      eventSubscription: null,
      showPrompt: false,
      disabledTerminal: false,
      promptMessage: "",
      leave: false,
      newCommand: "",
      collapsed: true
    };
  },
  methods: {
    closeMenu() {
      this.collapsed = true;
    },
    setAutoFocus() {
      if (this.$refs.terminalInput) this.$refs.terminalInput.focus();
    },
    navigateCommands(index) {
      this.newCommand = handler.navigateCommands(index);
    },
    handleCommand(incomingCommand = undefined) {
      if (incomingCommand) this.newCommand = incomingCommand;

      if (!this.disabledTerminal)
        handler.terminal(
          this.newCommand,
          this.jsonData,
          ({ info, component }) => {
            if (info === "clear") {
              this.commands = [];
              this.newCommand = "";
              return;
            }
            if (info === "Downloading CV ...") {
              this.disabledTerminal = true;
            }
            if (info === "exit") {
              router.push("/");
              return;
            }
            this.newLine({ info, component });
          }
        );
    },
    newLine({ info, component = "singlePrint" }) {
      const { prompt } = this.jsonData.terminal;

      this.commands.push({
        prompt: prompt,
        text: this.newCommand,
        component: "singlePrint"
      });
      this.newCommand = "";
      this.commands.push({
        prompt: "",
        data: info,
        component: component
      });
      setTimeout(() => {
        this.scrollToBottom();
      }, 10);
    },
    scrollToBottom() {
      let elem = this.$el.getElementsByClassName("blank_line")[0];
      elem.scrollIntoView({
        block: "start",
        inline: "end"
      });
    }
  },
  created() {},
  watch: {
    jsonData: function() {
      this.promptMessage = this.jsonData.terminal.prompt;
    }
  },
  mounted() {
    this.promptMessage = this.jsonData.terminal.prompt;
    let event = handler.event.asObservable();

    this.eventSubscription = event.subscribe(message => {
      this.commands.splice(this.commands.length, 0, {
        prompt: "",
        text: message === "error" ? this.getErrorMessage() : message,
        error: message === "error",
        component: "singlePrint"
      });
      this.disabledTerminal = false;
    });
  },
  beforeDestroy() {
    this.eventSubscription.unsubscribe();
  },
  beforeRouteLeave(to, from, next) {
    if (!this.leave && this.showPrompt) {
      this.disabledTerminal = true;
      this.leave = true;
      new Typed("#leaveMessage", {
        strings: [this.jsonData.commands.exit],
        typeSpeed: 40,
        showCursor: false,
        onComplete: () => {
          setTimeout(() => {
            next();
          }, 1500);
        }
      });
    } else next();
  }
};
</script>

