  color: #009b64ba;
  margin-top: 20vh;
  overflow: hidden;
}

.col {
  padding-left: 80px;
  overflow-x: hidden;
  padding-bottom: 0;
}

.historyText {
  white-space: pre-line;
  padding-left: 10px;
  position: relative;
  top: -1px;
  max-width: 90%;
  word-break: break-word;
}

.blank_line {
  min-height: 20px;
  margin-bottom: 20px;
}

.commandHistory {
  word-break: break-all;
}

.prompt {
  color: purple;
}

.terminalInput {
  width: 100%;
  flex: 1;
  outline: none;
  padding-left: 10px;
  position: relative;
  top: -1px;
}

.container {
  padding-top: 0;
  padding-bottom: 0;
}

.terminal {
  font-size: 2.7vh;
}

.container {
  padding-top: 0;
}

.terminal {
  font-size: 24px;
}

